.header{
    width: 100%;
    height: 500px;
    background: url(../img/silver-background-5-Best-Wallpaper.png)no-repeat center center/cover;
}


.header_flex{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(0,0,0,0.3);
    height: 80px;
    padding: 20px;
    position: fixed;
    box-shadow: 2px 2px 5px #999;
}
.logo img{
    width: 170px;
    
}

.nav ul{
    display: flex;
    

}
.nav a{
    padding: 20px;
    text-decoration: none;
    color: black;
}

.nav li{
    list-style: none;;
}

.header_content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    height: 100%;
    padding: 20px;
    font-size: 1.5em;
    line-height: 2.5em;
    color: rgba(0,0,0,0.4)
}

.button_header{
    background-color: rgba(0,0,0,0.3);
    padding: 0px 20px;
    border-radius: 5px;
    margin-top: 20px;
    color: white;
    box-shadow: 2px 2px 5px #999;
    /* box-shadow:5px 5px 5px #ccc;
    box-shadow: 0 0 0.7142857142857143rem #cccccc; */
    cursor: pointer;
}

@media(max-width:768px){

.header_content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    height: 100%;
    padding: 20px;
    font-size: 1.1em;
    line-height: 2.5em;
    color: rgba(0,0,0,0.4)

}
/* --------------------Menu responsive------------------ */
.nav {
    position: absolute;
    top:80px;
    left:-100%;
    width: 200px;
    transition: all 0.4s;
    height: 100vh;
    background-color: rgba(0,0,0,0.7);
    list-style: none;
    color: black;
    box-shadow: 2px 2px 5px #999;
  
  }
.mostrar {
    left: 0;
  }

  nav ul{
      flex-direction: column;
      display: flex;
      padding: 0;
  }
.nav ul li {
    
    margin-top: 20px;
  }
}

